#sib-container input:-ms-input-placeholder {
	text-align: left;
	font-family: "Helvetica", sans-serif;
	color: #c0ccda;
}

#sib-container input::placeholder {
	text-align: left;
	font-family: "Helvetica", sans-serif;
	color: #c0ccda;
}

#sib-container textarea::placeholder {
	text-align: left;
	font-family: "Helvetica", sans-serif;
	color: #c0ccda;
}

#sib-container {
	background: none;
	padding: 0;
}
.sib-form {
	padding: 0;
	max-width: 400px;
}
.signup-row {
	display: flex;
	align-items: center;
	gap: 10px;
	justify-content: space-between;
}
.signup-row > div {
}

.sib-form .input {
	color: black;
}

.sib-form .entry__field {
	border: none;
}
.sib-form-message-panel {
	margin: unset;
}